import React, { useState } from 'react'
import { MenuOutlined, PlusOutlined } from '@ant-design/icons';
import './Header.less'
import { Button, Drawer } from 'antd';
import Filters from './Filters/Filters';

const Header = () => {
    const [visible, setVisible] = useState(false);
  const showDrawer = () => {
    setVisible(true);
  };
  const onClose = () => {
    setVisible(false);
  };
  return (
    <div className='header'>
        <div className='menu'>
            <div><Button icon={<MenuOutlined />} type='text' shape='circle' onClick={showDrawer} size='large'></Button></div>
        </div>
        <div></div>
        <div className='new'><Button type='primary' shape='circle' icon={<PlusOutlined />} /></div>
        <Drawer title="Basic Drawer" placement="left" onClose={onClose} visible={visible} width={300}>
            <Filters />
        </Drawer>
    </div>
  )
}

export default Header