import { Button, Divider, Input, Space } from 'antd'
import React from 'react'
import './Note.less'
import { TNote } from '../../../types/types'
import { ShareAltOutlined, DeleteOutlined, UnorderedListOutlined, OrderedListOutlined, CheckSquareOutlined, CameraOutlined, AudioOutlined, LineHeightOutlined } from '@ant-design/icons';
import BlockParagraph from './Blocks/BlockParagraph/BlockParagraph';
import BlockHeader from './Blocks/BlockHeader/BlockHeader';


interface IProps {
    note: TNote
}

const Note = ({note}: IProps) => {
  return (
    <div className='note'>
        <div className='header'>
            <div className='datetime'>
                <h4>{note.created.toLocaleDateString()}</h4>
            </div>
            <div className='actions'>
                <Space size={'middle'}>
                    <Button type='link' icon={<ShareAltOutlined />} size='small'>Share</Button>
                    <Button type='text' icon={<DeleteOutlined />} size='small' />
                </Space>                
            </div>
        </div>
        {note.blocks?.map(block => {
            if (block.type === 'paragraph') {
                return <BlockParagraph text={block.data.text} />
            } else if (block.type === 'header') {
                return <BlockHeader text={block.data.text} />
            }
        })}
        <Divider style={{margin: '16px 0 5px 0'}} />
        <div className='format'>
            <Space size={'large'}>
                <Button type='text' icon={<LineHeightOutlined />} />
                <Button type='text' icon={<UnorderedListOutlined />} />
                <Button type='text' icon={<OrderedListOutlined />} />
                <Button type='text' icon={<CheckSquareOutlined />} />
                <Button type='text' icon={<CameraOutlined />} />
                <Button type='text' icon={<AudioOutlined />} />
            </Space>
        </div>
    </div>
  )
}

export default Note