import React from 'react'
import { TNote } from '../../../types/types'
import Note from '../Note/Note'

const notes: TNote[] = [
    {
        id: '1',
        created: new Date(),
        blocks: [
            {
                id: '1',
                type: 'header',
                data: {
                    text: 'Prochaines étapes',
                    level: 1
                }
            },
            {
                id: '2',
                type: 'paragraph',
                data: {
                    text: 'Les prochaines étapes à traiter en priorité sont:'
                }
            }
        ]
    }
]

const ListNotes = () => {
  return (
    <div>
        {notes.map((note: TNote) => {
            return (
                <div key={note.id}><Note note={note} /></div>
            )
        })}
    </div>
  )
}

export default ListNotes